import Home from './components/Home'
import Blog from './components/Blog'
import { createRouter, createWebHistory } from "vue-router"

const routeInfos = [
    {
        path : "/",
        component : Home
    },
    {
        path : "/blog",
        component : Blog
    },
    {
        path : "/:pathMatch(.*)*",
        component : Home
    },

]
    
const router = createRouter({
    history : createWebHistory(),
    routes : routeInfos
})
    
export default router;